import React from 'react';
import styled from 'styled-components';
import { useSpring, animated } from 'react-spring';

const WelcomeContainer = styled.section`
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: ${({ theme }) => theme.spacing.md};
  background-color: ${({ theme }) => theme.colors.background};
  background-image: url('../img/floral.png');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
  overflow: hidden;

  @media (max-width: ${({ theme }) => theme.breakpoints.mobile}) {
    padding: ${({ theme }) => theme.spacing.sm};
    background-size: 150% auto;
  }
`;

const GoldBorder = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border: 20px solid ${({ theme }) => theme.colors.secondary};
  opacity: 0.2;
  pointer-events: none;

  @media (max-width: ${({ theme }) => theme.breakpoints.mobile}) {
    border-width: 10px;
  }
`;

const ContentWrapper = styled.div`
  max-width: 800px;
  width: 90%;
  margin: 0 auto;
  padding: ${({ theme }) => theme.spacing.xl};
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);

  @media (max-width: ${({ theme }) => theme.breakpoints.mobile}) {
    width: 95%;
    padding: ${({ theme }) => theme.spacing.md};
  }
`;

const InviteText = styled(animated.p)`
  font-family: ${({ theme }) => theme.fonts.tertiary};
  color: ${({ theme }) => theme.colors.text};
  font-size: 1.2rem;
  margin-bottom: ${({ theme }) => theme.spacing.lg};
  text-transform: uppercase;
  letter-spacing: 2px;

  @media (max-width: ${({ theme }) => theme.breakpoints.mobile}) {
    font-size: 1rem;
    letter-spacing: 1px;
    margin-bottom: ${({ theme }) => theme.spacing.md};
  }
`;

const Title = styled(animated.h1)`
  font-family: ${({ theme }) => theme.fonts.primary};
  color: ${({ theme }) => theme.colors.primary};
  font-size: 4rem;
  margin-bottom: ${({ theme }) => theme.spacing.md};

  @media (max-width: ${({ theme }) => theme.breakpoints.mobile}) {
    font-size: 2.5rem;
    margin-bottom: ${({ theme }) => theme.spacing.sm};
  }

  @media (min-width: ${({ theme }) => theme.breakpoints.tablet}) {
    font-size: 5rem;
  }
`;

const Subtitle = styled(animated.h2)`
  font-family: ${({ theme }) => theme.fonts.secondary};
  color: ${({ theme }) => theme.colors.primary};
  font-size: 2rem;
  margin-bottom: ${({ theme }) => theme.spacing.xl};

  @media (max-width: ${({ theme }) => theme.breakpoints.mobile}) {
    font-size: 1.5rem;
    margin-bottom: ${({ theme }) => theme.spacing.lg};
  }
`;

const DateText = styled(animated.p)`
  font-family: ${({ theme }) => theme.fonts.secondary};
  color: ${({ theme }) => theme.colors.primary};
  font-size: 1.5rem;
  margin: ${({ theme }) => theme.spacing.lg} 0;

  @media (max-width: ${({ theme }) => theme.breakpoints.mobile}) {
    font-size: 1.2rem;
    margin: ${({ theme }) => theme.spacing.md} 0;
  }
`;

const IllustrationWrapper = styled.div`
  position: relative;
  width: 300px;
  height: 400px;
  margin: ${({ theme }) => theme.spacing.xl} auto;

  @media (max-width: ${({ theme }) => theme.breakpoints.mobile}) {
    width: 200px;
    height: 300px;
    margin: ${({ theme }) => theme.spacing.lg} auto;
  }
`;

const DressIllustration = styled.img`
  width: 100%;
  height: 100%;
  object-fit: contain;
`;

const Butterfly = styled(animated.img)`
  position: absolute;
  width: 50px;
  height: 50px;
  ${({ position }) => position};

  @media (max-width: ${({ theme }) => theme.breakpoints.mobile}) {
    width: 30px;
    height: 30px;
  }
`;

const WelcomeSection = ({ parents, name, date, image }) => {
  const fadeIn = useSpring({
    from: { opacity: 0, transform: 'translateY(30px)' },
    to: { opacity: 1, transform: 'translateY(0)' },
    config: { duration: 1000 },
  });

  const butterflyAnimation = useSpring({
    from: { transform: 'translate(0px, 0px)' },
    to: async (next) => {
      while (true) {
        await next({ transform: 'translate(10px, -10px)' });
        await next({ transform: 'translate(0px, 0px)' });
      }
    },
    config: { duration: 2000 },
  });

  return (
    <WelcomeContainer>
      <GoldBorder />
      <ContentWrapper>
        <InviteText style={fadeIn}>
          {parents} cordially invite you to celebrate the
        </InviteText>
        <Title style={fadeIn}>Quinceaera</Title>
        <Subtitle style={fadeIn}>of their daughter</Subtitle>
        <Title style={fadeIn}>{name}</Title>
        <DateText style={fadeIn}>{date}</DateText>
        
        <IllustrationWrapper>
          <DressIllustration src="../img/vestidoIA1.png" alt="Quinceaera dress" />
          <Butterfly 
            style={butterflyAnimation}
            src="../img/butterfly.png"
            alt="Decorative butterfly"
            position={{ top: '20%', right: '-20px' }}
          />
          <Butterfly
            style={butterflyAnimation}
            src="../img/butterfly.png"
            alt="Decorative butterfly"
            position={{ bottom: '30%', left: '-20px' }}
          />
        </IllustrationWrapper>
      </ContentWrapper>
    </WelcomeContainer>
  );
};

export default WelcomeSection;
